<template>
    <Textarea
        class="form-control"
        rows="1"
        :autoResize="true"
        :value="modelValue"
        @input="$emit('update:modelValue', $event.target.value)"
    />
</template>

<script lang="ts">
import { defineComponent } from "vue";

import Textarea from "primevue/textarea";

export default defineComponent({
    components: {
        Textarea,
    },
    props: {
        modelValue: String,
    },
});
</script>
