<template>
    <select
        class="form-select"
        :value="modelValue"
        @input="$emit('update:modelValue', $event.target.value)"
    >
        <option selected>&nbsp;</option>
        <option
            v-for="(item, index) in itemList"
            :key="index"
            :value="item.value"
        >
            {{ item.text }}
        </option>
    </select>
</template>

<script lang="ts">
import { defineComponent } from "vue";

export default defineComponent({
    props: {
        itemList: Array,
        modelValue: String,
    },
});
</script>
